import request from '@/util/request.js';

/**
 * 
 */
const getNowDate = () => {
	const myDate = new Date();
	let year = myDate.getFullYear();
	let month = myDate.getMonth()+1 < 10 ? '0'+(myDate.getMonth()+1) : myDate.getMonth()+1;
	let date = myDate.getDate() < 10 ? '0'+myDate.getDate() : myDate.getDate();
	return year+"-"+month+"-"+date
};
export { getNowDate }

/**
 * 
 * @param {Object} fmt 
 * @param {Object} date 
 */
const dateFormat = (fmt, date) => {
    let ret;
    const opt = {
        "Y+": date.getFullYear().toString(),        // 
        "m+": (date.getMonth() + 1).toString(),     // 
        "d+": date.getDate().toString(),            // 
        "H+": date.getHours().toString(),           // 
        "M+": date.getMinutes().toString(),         // 
        "S+": date.getSeconds().toString()          // 
        // 
    };
    for (let k in opt) {
        ret = new RegExp("(" + k + ")").exec(fmt);
        if (ret) {
            fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
        };
    };
    return fmt;
};
export { dateFormat }

/**
 * 
 * @param {Object} attr 
 * @param {Object} rev /
 */
const  sortBy = (attr,rev) => {
	// 
	if(rev ===  undefined){
		rev = 1;
	}else{
		rev = (rev) ? 1 : -1;
	}
	return function(a,b){
		a = a[attr];
		b = b[attr];
		if(a < b){
		  return rev * -1;
		}
		if(a > b){
		  return rev * 1;
		}
		return 0;
    }
};
export { sortBy }

const updateUserInfo = () => {
	request.post('/hs/login',{
		role: uni.getStorageSync("userInfo").user_type,
		username: uni.getStorageSync("userInfo").real_name,
		password: uni.getStorageSync("userInfo").pass_word
		}).then(res => {
			console.log("",res.data);
			uni.setStorageSync("userInfo", res.data);
		},err => {
		})
}
export { updateUserInfo }