
const baseUrl = 'http://106.53.113.158:8048';  		//
// const baseUrl = 'http://192.168.10.29:8048';  	//
// const baseUrl = 'http://127.0.0.1:8048'; 		//

const get = (url, data) => {
    let httpDefaultOpts = {
        url: baseUrl+url,
        data: data,
        method: 'get',
        header: {
        'X-Requested-With': 'XMLHttpRequest',
        "Accept": "application/json",
        "Content-Type": "application/json; charset=UTF-8"
    },
        dataType: 'json',
    }
    let promise = new Promise(function(resolve, reject) {
        uni.request(httpDefaultOpts).then((res) => {
        	if (res[1].data.success === true && res[1].data.data !== null) {
        			resolve(res[1].data)
        		} else {
					uni.showToast({
						icon: 'none',
						title: ''
					});
        		}
            }
        ).catch(
            (response) => {
                reject(response);
				uni.showToast({
					icon: 'none',
					title: ''
				});
            }
        )
    })
    return promise
};
const post = (url, data) => {
    let httpDefaultOpts = {
        url: baseUrl+url,
        data: data,
        method: 'post',
		header: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'application/json'
    },
        dataType: 'json',
    }
    let promise = new Promise(function(resolve, reject) {
        uni.request(httpDefaultOpts).then((res) => {
			if (res[1].data.success === true && res[1].data.data !== null) {
				if (res[1].data.data === 0) {
					uni.showToast({
						icon: "none",
						title: ""
					})
				} else {
					resolve(res[1].data)
				}
			} else {
				resolve({
					message: "",
					data: res[1].data
				});
			}
		}
	).catch(
		(response) => {
			reject(response)
		}
	)
})
    return promise
};
//Token
const httpTokenRequest = (opts, data) => {
	let token = "";
	uni.getStorage({
		key: 'token',
		success: function(ress) {
			token = ress.data
		}
	});
    //tokenstorage
    let httpDefaultOpts = {
        url: baseUrl+opts.url,
        data: data,
        method: opts.method,
        header: opts.method == 'get' ? {
		'Token': token,
        'X-Requested-With': 'XMLHttpRequest',
        "Accept": "application/json",
        "Content-Type": "application/json; charset=UTF-8"
    } : {
		'Token': token,
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
    },
        dataType: 'json',
    }
    let promise = new Promise(function(resolve, reject) {
        uni.request(httpDefaultOpts).then(
            (res) => {
                resolve(res[1])
            }
        ).catch(
            (response) => {
                reject(response)
            }
        )
    })
    return promise
};
 
export default {
	    baseUrl,
		get,
		post,
		httpTokenRequest
}